import React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import Box from "@material-ui/core/Box";
import Footer from "../components/Footer";
import AppBar from "../components/AppBar";
import { Link } from "gatsby";
import { FaLinkedin, FaGithub } from "react-icons/fa";

import { OutboundLink } from "gatsby-plugin-google-analytics";

import CourseList from "../components/CourseList";

import ProfilBild from "../images/AVATAR2.jpg";

const About = () => {
  return (
    <React.Fragment>
      <CssBaseline />

      <main>
        <AppBar />
        <Container maxWidth="md">
          <Box my={4}>
            <Container maxWidth="xs" align="center">
              <img
                style={{
                  display: "block",
                  width: "100%",
                  border: "1px solid #010A26",
                  marginLeft: "auto",
                  marginRight: "auto",
                  marginBottom: "40px",
                  borderRadius: "50%",
                }}
                src={ProfilBild}
                alt="Profile"
              />

              <OutboundLink
                rel="noopener noreferrer"
                target="_blank"
                href="https://github.com/FredrikErikJohansson"
                style={{ color: "inherit" }}
              >
                <FaGithub
                  size="60px"
                  style={{ marginLeft: "12px", marginRight: "12px" }}
                />
              </OutboundLink>
              <OutboundLink
                rel="noopener noreferrer"
                target="_blank"
                href="https://www.linkedin.com/in/fredrik-johansson-66364a179/"
                style={{ color: "inherit" }}
              >
                <FaLinkedin
                  size="60px"
                  style={{ marginLeft: "12px", marginRight: "12px" }}
                />
              </OutboundLink>
            </Container>
            <Typography
              component="h4"
              variant="h4"
              gutterBottom
              color="primary"
            >
              About
            </Typography>
            <Typography
              color="primary"
              paragraph
              gutterBottom
              style={{ marginBottom: "40px" }}
            >
              My name is Fredrik and I'm in my fifth year of working towards my{" "}
              <a
                rel="noopener noreferrer"
                target="_blank"
                href="https://liu.se/studieinfo/en/program/6cmen/4641#"
                style={{
                  color: "#F20544",
                  textDecoration: "inherit",
                }}
              >
                Master of Science degree in Media Technology and Engineering
              </a>
              . I'm currently looking for master thesis oppertunities, mainly
              within computer graphics, machine learning, artificial
              intelligence and heterogeneous computing. My past projects can be
              found under{" "}
              <Link
                to="/projects"
                style={{
                  color: "#F20544",
                  textDecoration: "inherit",
                }}
              >
                projects
              </Link>
              . The website itself is written in JavaScript with React and
              GatsbyJS. Feel free to contact me if you have any questions, my
              contact information can be found in the footer.
            </Typography>

            <Typography
              component="h5"
              variant="h5"
              gutterBottom
              color="primary"
            >
              Master's degree
            </Typography>
            <CourseList isMaster={true} />
            <Typography
              component="h5"
              variant="h5"
              gutterBottom
              color="primary"
              style={{ paddingTop: "40px" }}
            >
              Bachelor's degree
            </Typography>
            <CourseList isMaster={false} />
          </Box>
        </Container>
        <Footer />
      </main>
    </React.Fragment>
  );
};

export default About;
